import React from 'react';
import './Projects.css'

const Projects = () => {
    return(
        <React.Fragment>
            <div className="panel panel-info">
                <div className="panel panel-heading">Projects</div>
                <div className="panel panel-content container">
                    <div className="row">
                        <div className="col-md-2 col-lg-2 col-sm-12">
                            <a href="https://ithub-khammam.com" target="_blank"  rel="noreferrer" className="image-hover">
                                <img src="https://i.ibb.co/Y2kqZKt/ithub-khammam.png" alt="Avatar" className="project-image"/>
                                <div className="overlay">
                                    <div>
                                        <h3 className="project-text">IT HUB Khammam</h3>
                                        <h4 className="project-text2">Website for Jobs Hiring</h4>
                                    </div>
                                </div>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Projects;