import React,{Component} from 'react';
import './Contact.css';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

const contacts = "https://solvencycontact.herokuapp.com/Contacts";



class Contact extends Component{
    constructor(){
        super()

        this.state={
            order_id: Math.floor(Math.random() * (100 - 100 + 1) ) + 100,
            name:'',
            phone:'',
            email:'',
            message:''
        }

    }

    handleChangeName = (event) => {
        this.setState({name:event.target.value})
    }

    handleChangePhone =(event) => {
        this.setState({phone:event.target.value})
    }

    handleChangeEmail =(event) => {
        this.setState({email:event.target.value})
    }

    handleChangeMessage =(event) => {
        this.setState({message:event.target.value})
    }

    handleSubmit = () => {
       fetch(contacts,{
           method:'POST',
           headers:{
               'Accept':'application/json',
               'Content-Type':'application/json'
           },
           body:JSON.stringify(this.state)
       })
       .then((this.props.history.push('/contactsplace?message=Success')))
    }
    
    render(){
        console.log(this.props)
        return(
            <div className="container">
                <div className="row">
                    <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12">
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Contemplative Reptile"
                            height="160"
                            image="https://i.ibb.co/3S8NCJj/usa.jpg"
                            title="Headquarters - USA "
                            />
                            <CardContent>
                            <Typography gutterBottom variant="h4" component="h2">
                                Headquarters - USA 
                            </Typography>
                            <Typography variant="h5" color="textSecondary" component="h5">
                            205 Van Buren Street Suite 120 Herndon<br/>
                            VA 20170<br/>

                            Email: <a href="info@solvency.us">info@solvency.us</a><br/>

                            Sales : <a href="sales@solvency.us">sales@solvency.us</a><br/>

                            Career : <a href="career@solvency.us">career@solvency.us</a><br/>

                            Recruitment: <a href="recruiter@solvency.us">recruiter@solvency.us</a><br/>
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </div>
                    <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12 card-2" >
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="IT HUB- Khammam"
                            height="160"
                            image="https://i.ibb.co/YL6xjLM/IT-HUB.jpg"
                            title="Branch Office - India - Khammam"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="h4" component="h2">
                                Branch Office - India - Khammam
                            </Typography>
                            <Typography variant="h5" color="textSecondary" component="h5">
                            IT HUB, 5th Floor, Block-B,<br/>
                            Indira Nagar Colony,<br/>
                            Khammam, Telangana 507002, India.<br/>
                            Ph: <a href="9493316846">9493316846</a><br/>
                            Fax: 866 594 4637<br/>
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </div>
                </div>
                <div className="panel panel-primary class">
                    <div className="panel-heading">
                        <h4>Contact Us</h4>
                    </div>
                        <div class="address-left">
                            <p>Khammam<br/>Telangana <br/> India </p>
                        </div>
                        <div class="address-right">
                            <p>Call us : +91 9493316846 </p>
                            <p>E-mail : <a href="info@solvency.us">info@solvency.us</a></p>
                        </div>
                    <form>
                        <div>
                            <div className="form-group">
                                <input type="text" name="name" placeholder="Please Enter Your Name" value={this.state.name}
                                className="form-control" onChange={this.handleChangeName}/>
                            </div>
                            <div className="form-group">
                                <input type="text" name="phone" placeholder="Please Enter Your Phone with country code" value={this.state.phone}
                                className="form-control" onChange={this.handleChangePhone}/>
                            </div>
                            <div className="form-group">
                                <input type="text" name="email" placeholder="Please Enter Your Email" value={this.state.email}
                                className="form-control" onChange={this.handleChangeEmail}/>
                            </div>
                            <div className="form-group">
                                <label>Message</label>
                                <textarea type="textarea" name="message" placeholder="Message Us" value={this.state.message}
                                className="form-control" onChange={this.handleChangeMessage}/>
                            </div>
                           
                            <button className="btn btn-danger" type="clear">Clear </button>&nbsp;&nbsp;
                            <button className="btn btn-success" onClick={this.handleSubmit} >Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Contact;