import React from 'react';
import './Home.css';
import bgvideo from '../video/1.mp4';
import Zoom from 'react-reveal/Zoom';

const Home = () => {
    return(
        <React.Fragment>
            <div className="mid">
                <video autoPlay muted loop>
                    <source className="embed-responsive" src={bgvideo} type="video/mp4"/>
                </video>
                <div className="hero">
                    <h2 className="heading-text">
                        <Zoom left cascade>
                            Solvency Software Solutions
                        </Zoom>
                    </h2>
                    <p>Solvency Inc is an established Software Development <br/> Outsourcing Company in Information Technology.</p>
                    <a href="https://solvency.us/">Get Started</a>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Home;